.user {
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.user-login {
  color: white;
}
.user-word {
  outline: none;
  text-align: center;
  padding: 10px;
  border: none;
}
.user-word:disabled {
  background-color: white;
  border: none;
  opacity: 1;
}
.word-wrapper {
  position: relative;
}
.word-button {
  position: absolute;
  background: green;
  color: black;
  border: none;
  font-size: 15px;
  width: 30px;
  height: 30px;
  right: 0;
  top: 70%;
}
.cancel-word {
  background: red;
  transform: translateX(-100%);
}
