#root {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  background: #272e51;
}
* {
  list-style: none;
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: "Comic Sans MS", "Comic Sans", "Arial", cursive;
}
.avatar {
  width: 100px;
  height: auto;
  border-radius: 50%;
  background-color: #ffc466;
}
