.login-screen {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-screen > * {
  margin-right: 5px;
}

.login-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-error {
  color: red;
  font-size: 12px;
}
.avatar-wrapper {
  position: relative;
}
.login-wrapper * {
  margin-bottom: 5px;
}
.refresh-avatar {
  position: absolute;
  font-size: 30px;
  background: transparent;
  border: none;
  color: white;
  border-radius: 50%;
  text-shadow: 0 0 3px black, 0 0 5px black;
  top: 0;
  right: 0;
  cursor: pointer;
}
